package strings

import io.github.skeptick.libres.strings.PluralForms
import io.github.skeptick.libres.strings.getCurrentLanguageCode
import kotlin.String
import kotlin.collections.Map

public object ResStrings {
  private val baseLocale: StringsEn = StringsEn

  private val locales: Map<String, Strings> = mapOf("de" to StringsDe, "tr" to StringsTr, "en" to
      StringsEn)

  public val home: String
    get() = locales[getCurrentLanguageCode()]?.home ?: baseLocale.home

  public val about: String
    get() = locales[getCurrentLanguageCode()]?.about ?: baseLocale.about

  public val contact: String
    get() = locales[getCurrentLanguageCode()]?.contact ?: baseLocale.contact

  public val book_stands: String
    get() = locales[getCurrentLanguageCode()]?.book_stands ?: baseLocale.book_stands

  public val section1_title: String
    get() = locales[getCurrentLanguageCode()]?.section1_title ?: baseLocale.section1_title

  public val section1_button: String
    get() = locales[getCurrentLanguageCode()]?.section1_button ?: baseLocale.section1_button

  public val section1_desc: String
    get() = locales[getCurrentLanguageCode()]?.section1_desc ?: baseLocale.section1_desc

  public val section2_title: String
    get() = locales[getCurrentLanguageCode()]?.section2_title ?: baseLocale.section2_title

  public val section2_button: String
    get() = locales[getCurrentLanguageCode()]?.section2_button ?: baseLocale.section2_button

  public val section2_desc: String
    get() = locales[getCurrentLanguageCode()]?.section2_desc ?: baseLocale.section2_desc
}
